  width: 1px;
  background: #EEEEEE;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.vertical-line.vertical-line-400:after {
  background-color: #E0E0E0;
}

/* -------------------------------------------------------------------------- */
/*                                 Transition                                 */
/* -------------------------------------------------------------------------- */
.transition-base {
  transition: all 0.2s ease-in-out;
}

.transition-none {
  transition: none;
}

.fsp-75 {
  font-size: 75%;
}

/* -------------------------------------------------------------------------- */
/*                                    Width                                   */
/* -------------------------------------------------------------------------- */
.min-w-0 {
  min-width: 0;
}

/* -------------------------------------------------------------------------- */
/*                                Terms sidebar                               */
/* -------------------------------------------------------------------------- */
.icon {
  padding: 1rem;
  border-radius: 15px;
  display: inline-flex;
  background-color: rgba(255, 254, 254, 0.2);
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 254, 254, 0.3);
}

.icon-primary {
  color: #747DEF;
}

.icon-secondary {
  color: #544837;
}

.icon-success {
  color: #79B93C;
}

.icon-info {
  color: #567592;
}

.icon-warning {
  color: #FF9900;
}

.icon-danger {
  color: #DF6951;
}

.icon-light {
  color: #F0F4F9;
}

.icon-dark {
  color: #979797;
}

/* -------------------------------------------------------------------------- */
/*        Heading                        Background                               */
/* -------------------------------------------------------------------------- */
.footer-heading-color {
  color: #080809;
}

.shadow-social {
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.1) !important;
}

.backdrop {
  backdrop-filter: blur(4px);
}

.navbar {
  transition: padding 0.3s linear;
}

.padding-transition {
  padding: 1.25rem !important;
}

.show-onhover:hover .hideEl {
  visibility: visible;
  opacity: 1;
}

.show-onhover .hideEl {
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.15s linear;
}

.shadow-hover {
  transition: all 0.2s ease-in-out;
  box-shadow: none;
}

.shadow-hover:hover {
  box-shadow: 0 6.25rem 5rem 0 rgba(0, 0, 0, 0.02), 0 4.05063rem 2.92813rem 0 rgba(0, 0, 0, 0.0152), 0 2.4075rem 1.5925rem 0 rgba(0, 0, 0, 0.0121), 0 1.25rem 0.8125rem 0 rgba(0, 0, 0, 0.01), 0 0.50938rem 0.4075rem 0 rgba(0, 0, 0, 0.0079), 0 0.11563rem 0.19687rem 0 rgba(0, 0, 0, 0.0048);
}

.round-btn-lg {
  height: 3.25rem;
  width: 3.25rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0;
}

.btn-secondary-outline {
  color: #173A56;
  border-color: #BCD0E5;
  border: 0.0625rem solid #BCD0E5;
}

.primary-btn-shadow {
  box-shadow: 0 1.25rem 2.1875rem 0 rgba(241, 165, 1, 0.15);
}

.danger-btn-shadow {
  box-shadow: 0 0.9375rem 1.875rem 0 rgba(223, 105, 81, 0.3);
}

.orange-gradient-btn {
  background-image: linear-gradient(to bottom, #FF946D, #FF7D68);
  border: none;
  border-radius: 0.625rem;
  padding: 1rem 2.5rem;
}

.orange-gradient-btn:hover, .orange-gradient-btn:focus {
  background-image: linear-gradient(to bottom, #FF7D68, #FF946D);
}

/* Hover */
.hover-top {
  transform: translateY(0) translateZ(0);
  transition: all 0.5s ease;
}

.hover-top.hover-top-shadow-lg:hover, .hover-top.hover-top-shadow-lg:focus {
  transform: translateY(-0.3125rem) translateZ(0);
}

.hover-top:hover, .hover-top:focus {
  transform: translateY(-0.125rem) translateZ(0);
  box-shadow: 0.5rem 0.5rem 1.5rem rgba(110, 74, 156, 0.1) !important;
  background-color: #D42DA3;
  color: #FFFEFE;
}

.hover-top:hover .heading-color, .hover-top:focus .heading-color {
  color: #FFFEFE;
}

/* Icon Forward */
.hvr-icon-forward {
  transition-duration: 0.1s;
}

.hvr-icon-forward .hover-icon {
  transform: translateZ(0);
  transition-duration: 0.1s;
  transition-property: transform;
  transition-timing-function: ease-out;
}

.hvr-icon-forward:hover .hover-icon, .hvr-icon-forward:focus .hover-icon, .hvr-icon-forward:active .hover-icon {
  transform: translateX(4px);
}

/* -------------------------------------------------------------------------- */
/*                                    Badge                                   */
/* -------------------------------------------------------------------------- */
.carousel-indicators {
  right: unset;
  margin-left: 1.2rem;
}

@media (max-width: 991.98px) {
  .carousel-indicators {
    transform: translateY(6rem);
    right: 0;
    margin-left: 15%;
  }
}

.carousel-indicators [data-bs-target] {
  background-color: #979797;
  width: 0.6875rem;
  height: 0.6875rem;
  opacity: 0.3;
  border-radius: 50%;
}

.carousel-indicators [data-bs-target]:not(:last-child) {
  margin-right: 1.5625rem;
}

.carousel-indicators [data-bs-target].active {
  opacity: 1;
}

.carousel-inner {
  overflow: unset;
}

.hero-title {
  font-family: "Poppins", "Rubik", -apple-system, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-weight: 700;
  font-size: 2.88651rem;
  color: #2F281E;
  margin-bottom: 5px;
  position: relative;
  font-size: 2.88651rem;
}

@media (min-width: 1200px) {
  .hero-title {
    font-size: 2.88651rem;
  }
}

.hero-img {
  width: 105%;
  margin-left: -5rem;
}

@media (min-width: 768px) and (max-width: 991.98px) {
  .hero-img {
    width: 135%;
    margin-left: -4rem;
  }
}

.card {
  border: none;
  background-color: transparent;
}

.input-group-icon {
  position: relative;
}

.input-group-icon .input-box {
  padding-left: 2.8rem;
  vertical-align: middle;
}

.input-group-icon .input-box-icon {
  position: absolute;
  top: 50%;
  left: 1rem;
  color: #212529;
  transform: translateY(-50%);
}

.form-control-border {
  border-radius: 0px;
  color: #BCD0E5;
}

.majestic-tabs {
  border-bottom: 0;
}

.majestic-tabs .nav-link {
  border: 2px solid transparent;
  color: #979797;
}

.majestic-tabs .nav-link:hover, .majestic-tabs .nav-link:focus {
  border-color: transparent;
}

.majestic-tabs .nav-link.active,
.majestic-tabs .nav-item.show .nav-link {
  border-bottom-color: #DF6951;
}

